/*------------------------------------*\
  #RESET
\*------------------------------------*/
/**
 * A very simple reset that sits on top of Normalize.css.
 */
body,
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
dl, dd, ol, ul,
form, fieldset, legend,
figure,
table, th, td, caption,
hr {
  margin: 0;
  padding: 0; }

/**
 * Remove trailing margins from nested lists.
 */
li > ul,
li > ol {
  margin-bottom: 0; }

/**
 * Remove spaces between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*------------------------------------*\
  #LINKS
\*------------------------------------*/
/**
 * Default styles for simple hyperlinks.
 */
a {
  color: #ff6b6b; }
  a:hover, a:focus {
    text-decoration: underline; }

/*------------------------------------*\
  #LISTS
\*------------------------------------*/
nav li {
  list-style-type: none; }

/*------------------------------------*\
  #PAGE
\*------------------------------------*/
/**
 * Page-level styling (e.g. HTML and BODY elements).
 */
body {
  padding: 2rem 2rem 6.75rem; }
  @media only screen and (min-width: 28.125rem) {
    body {
      padding: 2rem 2rem; } }

body {
  font-family: "Public Sans", sans-serif; }
  @supports (font-variation-settings: normal) {
    body {
      font-family: "Public Sans VF", sans-serif; } }

.c-hola {
  margin: -0.3125rem;
  font-family: 'Titillium Web', Helvetica, sans-serif;
  font-size: 2rem;
  font-weight: 900;
  text-transform: uppercase; }

.c-hola__la {
  font-size: 2.25rem; }

.c-hola__\! {
  transform: scale(1.3, 2.22); }

.c-logo {
  width: 2.625rem;
  vertical-align: middle; }

.c-nav--primary {
  display: flex;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  align-items: center;
  height: 5.5rem; }
  .c-nav--primary::after {
    z-index: -1;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3.125rem;
    background-color: #ff6b6b;
    content: ''; }
  @media only screen and (min-width: 28.125rem) {
    .c-nav--primary {
      position: relative;
      height: auto;
      justify-content: space-between; }
      .c-nav--primary::after {
        content: none; } }

.c-nav--primary__home {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  width: 5.5rem;
  margin: 0 auto;
  text-align: center; }
  @media only screen and (min-width: 28.125rem) {
    .c-nav--primary__home {
      position: static;
      width: auto;
      margin: 0; } }

.c-nav--primary__home__link {
  display: block;
  padding: 1rem 1rem 2rem;
  border-radius: 50%;
  background-color: #ff6b6b; }
  @media only screen and (min-width: 28.125rem) {
    .c-nav--primary__home__link {
      background-color: transparent;
      padding: 1rem; } }

.c-nav--primary__list {
  display: flex;
  width: 100%;
  padding: 0 0.5rem;
  background-color: #ff6b6b; }
  @media only screen and (min-width: 28.125rem) {
    .c-nav--primary__list {
      width: auto; } }

.c-nav--primary__item:nth-child(2) {
  margin-right: auto; }
  @media only screen and (min-width: 28.125rem) {
    .c-nav--primary__item:nth-child(2) {
      margin-right: 0; } }

.c-nav--primary__link {
  display: inline-block;
  padding: 0.75rem 2vw;
  color: #1D1D1E;
  font-size: 0.75rem;
  font-weight: 600;
  text-decoration: none;
  text-transform: uppercase;
  opacity: .8;
  transition: all 0.3s ease; }
  .c-nav--primary__link:hover, .c-nav--primary__link:focus {
    transform: translateY(-0.03125rem);
    text-decoration: none;
    opacity: 1; }
  .c-nav--primary__link:active {
    transform: translateY(0.03125rem); }
